@charset "UTF-8";
.detailWrapper {
  display: flex;
  height: 100vh;
  flex-direction: column;
  padding-bottom: 46px;
  box-sizing: border-box; }

.detailWrapper2 {
  display: flex;
  height: 100vh;
  flex-direction: column;
  padding-bottom: 98px;
  box-sizing: border-box; }

.main {
  flex: 1;
  overflow-y: scroll; }

.bottom {
  width: 100%;
  position: fixed;
  bottom: 0; }

.bottom2 {
  width: 100%;
  position: fixed;
  bottom: 55px;
  background-color: #fff;
  border-top: 1px solid #ddd; }

.more {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 12px;
  width: 18px;
  height: 5px; }

.bgWrapper {
  position: relative;
  font-size: 0; }
  .bgWrapper .bg {
    width: 100vw; }
  .bgWrapper .orderStatus {
    font-size: 16px;
    position: absolute;
    top: 50%;
    left: 24px;
    color: white;
    transform: translateY(-50%); }

.skuList {
  padding: 10px 16px;
  display: flex; }
  .skuList .skuWrapper {
    flex: 1;
    display: flex;
    justify-content: space-between; }
  .skuList .img {
    width: 60px;
    height: 60px; }
  .skuList .skuInfo {
    font-size: 12px;
    font-weight: 400;
    color: #636363;
    margin-left: 12px; }
    .skuList .skuInfo .title {
      width: 200px;
      /*必须是固定宽度值*/
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
      /*超出部分隐藏*/ }
  .skuList .priceInfo {
    text-align: right;
    font-weight: 400;
    color: #636363; }

.orderInfo {
  margin-top: 10px; }
  .orderInfo .title {
    border-left: 2px #f40c0c solid;
    padding-left: 5px; }

.list {
  padding-bottom: 10px;
  padding-left: 14px;
  padding-right: 14px; }

.item {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  font-size: 12px;
  font-weight: 400;
  color: #636363; }
  .item .arrow {
    width: 7px;
    height: 12px; }

.btns {
  padding: 10px 15px;
  text-align: right; }
  .btns .recevie,
  .btns .pay {
    outline: none;
    border: none;
    width: 68px;
    height: 22px;
    border-radius: 21px;
    font-size: 12px;
    color: white; }
  .btns .recevie {
    background: #ffb346;
    margin-right: 10px; }
  .btns .pay {
    background: #f1231f; }

.spuDetailImg {
  width: 100%; }

.listItem {
  padding: 8px 15px;
  display: flex;
  border-bottom: 1px solid #ddd; }
  .listItem .listLabel {
    font-size: 14px;
    font-weight: 400;
    color: #999999;
    min-width: 80px; }
  .listItem .input {
    outline: none;
    border: none; }

.addAddressWrapper {
  font-size: 14px;
  padding: 15px;
  display: flex;
  align-items: center; }
  .addAddressWrapper .img {
    width: 22px;
    height: 22px; }
  .addAddressWrapper .addAddressTitle {
    margin-left: 10px; }

.login {
  padding: 12px; }
  .login .inputItem, .login .linkItem {
    display: flex;
    align-items: center;
    padding: 8px 0;
    border-bottom: 1px solid #ddd; }
  .login .linkItem {
    justify-content: space-between; }
  .login .inputLabel {
    width: 80px; }
  .login .input {
    border: none;
    outline: none; }
  .login .labelArrow {
    width: 10px;
    height: 18px; }
  .login .registBtn {
    justify-content: flex-end;
    border-bottom: none; }
